

<template>
    <div>
        <span>经度{{ location.longitude.toFixed(2) }}</span>
        <el-divider direction="vertical"></el-divider>
        <span>纬度{{ location.latitude.toFixed(2) }}</span>
        <el-divider direction="vertical"></el-divider>
        <span>定位精度{{ location.accuracy.toFixed(2) }}</span>
        <el-divider direction="vertical"></el-divider>
        <i class="el-icon-refresh" @click="refreshCoord"></i>
    </div>
</template>

<script>
//获取经纬度的UI组件
export default {
    name: "LocationWidget",
    props: {
        value: {
            type: String,
            default: "{'latitude':22,'longitude':112,'accuracy':22222}"
        }
    },
    methods:{
        refreshCoord:function(){
            let that = this;
            if (navigator && navigator.geolocation) {
            } else {
                return;
            }
            navigator.geolocation.getCurrentPosition(function (position) {
                debugger
                if (position && position.coords) {
                    that.location = {
                        latitude:position.coords.latitude,
                        longitude:position.coords.longitude,
                        accuracy:position.coords.accuracy
                    }; 
                }
         })
        }
    },
    mounted() {
        this.refreshCoord();
    },
    data() {
        return {
             location:{'latitude':22,'longitude':112,'accuracy':22222}
        }
    },
    computed: {
        // rdata(){
        //     return 
        // }
    }, watch: {
        value: {
            handler(newval, oldval) {
               
            },
            deep: true,
        }
    }
}
</script>
