import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

const routes = [
  {
    path: "/forms/:group",
    name: "表单",
    component: () => import("@/views/Configforms.vue"),
  },
  {
    path: "/forms",
    name: "表单",
    component: () => import("@/views/Configforms.vue"),
  }, 
  {
    path: "/formseditor",
    name: "表单编辑器",
    component: () => import("@/views/formEditor.vue"),
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

export default router;
