//获取本地记录的list未提交表单
export let getlocalforms=function(){
    if (!localStorage) {
      return Promise.resolve([]);
    }
    //返回列表
    var obj = window.localStorage.getItem("tempformslist");
    if (obj) {
      return Promise.resolve(JSON.parse(obj));
    }else{
        window.localStorage.setItem("tempformslist", JSON.stringify([]));
    }
    return Promise.resolve([]);
}

//创建一个新的forms，使用guid进行标志，并添加到集合列表中
export let addlocalforms=function(item){
    
    if (!localStorage) {
      return Promise.resolve(false);
    }
    var obj = window.localStorage.getItem("tempformslist");
    if (obj) {
      let list = JSON.parse(obj);
      
      if (list.length > 10) return Promise.resolve(false);
      if (!item.guid) {
        item.guid = GUID();
      }
      //创建的时间
      if(!item.createtime){
        //item.createtime=
      }
      list.push(item);
      window.localStorage.setItem("tempformslist", JSON.stringify(list));
      return Promise.resolve(item);
    }
    return Promise.resolve(false);
}

//保存已有数据到localstorage中
export let savelocalforms=function(item){
    if (!localStorage) {
      return Promise.resolve(false);
    }
    var obj = window.localStorage.getItem("tempformslist");
    if (obj) {
      let list = JSON.parse(obj);
      //如果没有guid将无法进行保存
      if (!item.guid) {
        return Promise.resolve(false);
      }
      //修改item的值
      list=list.filter(i=>i.guid!=item.guid)
      list.push(item);
      window.localStorage.setItem("tempformslist", JSON.stringify(list));
      return Promise.resolve(item);
    }
    return Promise.resolve(false);
}


//移除一个表单,通过guid进行移除
export let removelocalforms=function(item)
{
    if (!localStorage) {
        return Promise.resolve(false);
    }
    var obj = window.localStorage.getItem("tempformslist");
    if (obj) {
      let list=JSON.parse(obj);
      list=list.filter(i=>i.guid!=item.guid)
      window.localStorage.setItem("tempformslist", JSON.stringify(list));
      return Promise.resolve(true);
    }
    return Promise.resolve(false);
}



