//分页查询已提交表单,包含组名
export let getForms=function(pageindex,pagesize,group){
  //默认查询10个
  const url=window.BaseUrl+`/MoveMapApply/forms/getformData`;
  let params={
    pageindex:pageindex,
    pagesize:pagesize,
    group:group,
  }
  return request({
      url:`${url}`,
      method: "get",
     params:params
  }).then(data=>{
      return data;
  }).catch(err=>{
      //如果未查询到数据返回默认表单
      alert("查询失败，请重试！")
  })
}



export let getDictionary = function (type) {
    //首先查找本地资源
    // var obj = window.localStorage.getItem("dictionarylist");
    // if(obj){
    //     obj = JSON.parse(obj);
    //     obj[type]
    // }
    

  let url = window.BaseUrl + `/MoveMapApply/forms/getDictionary`;
  if (window.dictionUrl) {
    url = window.dictionUrl;
  }
  let params = {
    type: type,
  };
  return request({
    url: `${url}`,
    method: "get",
    params: params,
  })
    .then((data) => {
      if (data.isok) {
        //更新到缓存中
        var obj = window.localStorage.getItem("dictionarylist");
        if (!obj) obj = '{}';
        obj = JSON.parse(obj);
        obj[type] = data.data;
        window.localStorage.setItem("dictionarylist", JSON.stringify(obj));
      }
      return data;
    })
    .catch((err) => {
      //如果未查询到数据返回默认表单
      //alert("查询失败，请重试！")
    });
};


