

<template>
    <div>
        <span>{{ location[posType].toFixed(3) }}</span>
        <el-divider direction="vertical"></el-divider>
        <!-- <span>纬度{{ location.latitude.toFixed(2) }}</span>
        <el-divider direction="vertical"></el-divider>
        <span>定位精度{{ location.accuracy.toFixed(2) }}</span> -->
        <!-- <el-divider direction="vertical"></el-divider> -->
        <i class="el-icon-refresh" @click="refreshCoord"></i>
    </div>
</template>

<script>
//获取经纬度的UI组件，扩展组件可同时支持经度或维度的单字段刷新，通过type进行指定
export default {
    name: "LocationWidgetX",
    props: {
        value: {
            type: Number,
            default: 112
        },
        posType:{
            type: String,
            default: "longitude"
        },
        //扩展其参考系
        sps:{
            type: String,
            default: "EPSG:4326"
        }
    },
    methods:{
        refreshCoord:function(){
            let that = this;
            if (navigator && navigator.geolocation) {
            } else {
                return;
            }
            navigator.geolocation.getCurrentPosition(function (position) {
                
                if (position && position.coords) {
                    that.location = {
                        latitude:position.coords.latitude,
                        longitude:position.coords.longitude,
                        accuracy:position.coords.accuracy
                    }; 
                    //刷新之后触发值得更新
                    if (that.$listeners.input) {
                        that.$listeners.input(that.location[that.posType]);
                    }
                }
         })
        }
    },
    mounted() {
        //赋值初始值
        this.location[this.posType]=this.value;
    },
    data() {
        return {
             location:{'latitude':22,'longitude':112,'accuracy':22222}
        }
    },
    computed: {
        // rdata(){
        //     return 
        // }
    }, watch: {
        value: {
            handler(newval, oldval) {
               debugger
            },
            deep: true,
        }
    }
}
</script>
