

<template>
    <div >
        <span>{{ provinceName}}</span>
        <el-divider direction="vertical"></el-divider>
        <span>{{ cityName}}</span>
        <el-divider direction="vertical"></el-divider>
        <span>{{ countryName}}</span>
        <el-divider direction="vertical"></el-divider>
        <i class="el-icon-refresh" @click="refreshCoord"></i>
        <van-popup v-model:show="showpop" :style="{ padding: '64px' }">
            <van-area  @confirm="handleconfirm" @cancel="handleconfirm" v-model="citycode" title="标题" :area-list="areaList" />
        </van-popup>
    </div>
</template>

<script>
import { areaList } from '@vant/area-data';
//获取或选中地址的组件
export default {
    name: "AddressWidget",
    props: {
        value: {
            type: String,
            default: "330302"
        }
    },
    methods:{
        refreshCoord:function(){
            this.showpop=true;
        },
        handleconfirm:function(selectedValues, selectedOptions){
            this.citycode=selectedValues[2].code;
            //this.value=this.citycode;
            this.showpop=false;
        }
    },
    mounted() {
        this.citycode=this.value;
    },
    data() {
        return {
            showpop:false,
             areaList:areaList,
             citycode:"330302",
        }
    },
    computed: {
        //区县名称
        cityName:function(){
            return areaList.city_list[this.citycode.substring(0,4)+"00"];
        },
        //省名称
        provinceName:function(){
            return areaList.province_list[this.citycode.substring(0,2)+"0000"];
        },
        //市名称
        countryName:function(){
            return areaList.county_list[this.citycode];
        }
    }, watch: {
        citycode: {
            //处理数据变化
            handler(newval, oldval) {
                if (this.$listeners.input) {
                    this.$listeners.input(newval)
                }
            },
            deep: true,
        }
    }
}
</script>
