<template>
    <el-select v-bind="$attrs" v-on="$listeners">
        <el-option v-for="(item, index) in enumOptions" :key="index" :label="item.code+'-'+item.name" :value="item.code">
        </el-option>
    </el-select>
</template>

<script>
import {getDictionary} from "../../../../api/forms"
/* 
扩展版选择器组件，可网络请求需要的数据字典，策略先请求本地的数据字典,如果没有查询到再请求web。
*/
export default {
    name: 'SelectWidgetDic',
    props: {
        enumType: {
            default: () => "dlbm2",
            type:String
        }
    },
    data(){
        return {
            enumOptions:[],
        }
    },
    mounted(){
        getDictionary(this.enumType).then(r => {
            if (r&&r.isok) {
                this.enumOptions = r.data;
            }
        })
    }
};
</script>
